    var dateUtils = require('../../common/util.js').dateUtils;

    export default {
        data() {
            return {
                banner: {},
                listData: [],
                last_id: "",
                reload: false,
				pn:0
            }
        },
        onLoad() {
            this.getBanner();
            this.getList();
        },
        onPullDownRefresh() {
            this.reload = true;
            this.last_id = "";
            this.getBanner();
            this.getList();
        },
        onReachBottom() {
            this.getList();
        },
        methods: {
            getBanner() {
                let data = {
                    column: "id,post_id,title,author_name,cover,published_at" //需要的字段名
                };
                uni.request({
                    url: uni.getStorageSync('hostname') + '?action=index/home/brand_banner',
                    data: data,
					method:'POST',
					dataType:'json',
					header: {'content-type' : 'application/x-www-form-urlencoded'},
                    success: (data) => {
                        uni.stopPullDownRefresh();
                        if (data.statusCode == 200) {
                            this.banner = data.data.one;
                        }
                    },
                    fail: (data, code) => {
                        console.log('fail' + JSON.stringify(data));
                    }
                })
            },
            getList() {
                var data = {
                    column: "id,post_id,title,author_name,cover,published_at" //需要的字段名
                };
                if (this.last_id) { //说明已有数据，目前处于上拉加载
                    data.minId = this.last_id;
                    data.time = new Date().getTime() + "";
                    data.pageSize = 10;
					data.pn = this.pn;
                }
                uni.request({
                    url: uni.getStorageSync('hostname') + '?action=index/home/brand',
                    data: data,
					method:'POST',
					dataType:'json',
					header: {'content-type' : 'application/x-www-form-urlencoded'},
                    success: (data) => {
                        if (data.statusCode == 200) {
                            let list = this.setTime(data.data.list);
                            this.listData = this.reload ? list : this.listData.concat(list);
                            this.last_id = list[list.length - 1].id;
                            this.reload = false;
                        }
                    },
                    fail: (data, code) => {
                        console.log('fail' + JSON.stringify(data));
                    }
                })
            },
            goDetail: function(e) {
                let detail = {
                    author_name: e.author_name,
                    cover: e.cover,
                    id: e.id,
                    post_id: e.id,
                    published_at: e.published_at,
                    title: e.title
                }
                uni.navigateTo({
                    url: "/pages/news/details?detailDate=" + encodeURIComponent(JSON.stringify(detail))
                })
            },
            setTime: function(items) {
                var newItems = [];
                items.forEach((e) => {
                    newItems.push({
                        author_name: e.article_author,
                        cover: e.resp_img,
                        id: e.id,
                        post_id: e.id,
                        published_at: e.article_date,
                        title: e.article_title
                    });
                });
                return newItems;
            }
        },
    }
